.zoomContainer {
  overflow: hidden;
}
.tools {
  position: absolute;
  top: 4px !important;
  left: 150px !important;
  z-index: 99;
  margin: 5px;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.tools > *:first-child {
  margin-right: 5px;
}
.tools > .ant-radio-button-wrapper > span > i {
  color: #6D757A;
}
@media only screen and (max-width: 414px) {
  .tools {
    position: absolute;
    top: -2px !important;
    left: 100px !important;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
}
@media only screen and (min-width: 414px) and (max-width: 675px) {
  .tools {
    position: absolute;
    top: -2px !important;
    left: 100px !important;
    z-index: 99;
    margin: 5px;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
}
.fs-split {
  /* padding-left: 6px; */
  background-color: #f4f4f4;
  height: 24px;
  text-align: center;
  float: right;
}
.fs-zoom {
  font-size: 10px;
  padding: 5px;
  background-color: #f4f4f4;
  height: 24px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #bec1c3;
  float: right;
}
.fs-zoom:hover {
  color: #e58f46;
}
.fs-zoom:hover .toolSort {
  background: url(data:image/png;base64,) center no-repeat;
  background-size: 9px 9px;
  background-color: #f4f4f4;
}
.fs-zoom .ant-dropdown-link {
  display: flex;
  justify-content: center;
  align-items: center;
}
.fs-zoom:hover {
  cursor: pointer;
}
.bottomTools {
  width: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 30px;
  background: #f4f4f4;
  bottom: -1px;
  right: 0px;
  position: fixed;
}
.bottomTools button {
  border: none;
  border-radius: 0px;
  padding: 0 2px;
}
.bottomTools a {
  color: #6f757a;
}
.bottomTools .toolBtn {
  width: 24px;
  height: 24px;
  padding-left: 9px;
  padding-right: 9px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.bottomTools .toolBtn:hover {
  background-size: 18px 18px;
  background-color: #f4f4f4;
}
.bottomTools .toolPointer {
